import React, { useState, useEffect, useRef } from 'react';
import { 
  Typography, 
  IconButton, 
  Box, 
  CircularProgress, 
  Fade,
  Zoom,
  Tooltip,
  Avatar,
  Button,
  Paper,
  Switch,
  FormControlLabel
} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import MicIcon from '@mui/icons-material/Mic';
import MicOffIcon from '@mui/icons-material/MicOff';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import VolumeOffIcon from '@mui/icons-material/VolumeOff';
import CloseIcon from '@mui/icons-material/Close';
import SmartToyIcon from '@mui/icons-material/SmartToy';
import QuestionAnswerIcon from '@mui/icons-material/QuestionAnswer';
import LoginIcon from '@mui/icons-material/Login';
import PersonAddIcon from '@mui/icons-material/PersonAdd';
import axios from 'axios';
import { useAuth } from '../../context/AuthContext';
import { useNavigate } from 'react-router-dom';

const Chatbot = ({ show, onClose }) => {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [suggestedQuestions, setSuggestedQuestions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showSuggestions, setShowSuggestions] = useState(true);
  const [isListening, setIsListening] = useState(false);
  const [voiceEnabled, setVoiceEnabled] = useState(false);
  const [isSpeaking, setIsSpeaking] = useState(false);
  const [audioQueue, setAudioQueue] = useState([]);
  const messagesEndRef = useRef(null);
  const audioRef = useRef(null);
  const { isAuthenticated, user } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    // Fetch suggested questions when component mounts
    const fetchSuggestedQuestions = async () => {
      try {
        const res = await axios.get('/api/chatbot/suggested-questions');
        setSuggestedQuestions(res.data.suggestedQuestions);
        console.log('Loaded suggested questions:', res.data.suggestedQuestions);
      } catch (error) {
        console.error('Error fetching suggested questions:', error);
        // Set some default questions as fallback
        setSuggestedQuestions([
          'How do I submit my life certificate?',
          'Why is my pension payment delayed?',
          'How do I update my bank details?',
          'What documents are required for family pension?',
          'How can I check my pension status online?'
        ]);
      }
    };

    // Fetch chat history if user is authenticated
    const fetchChatHistory = async () => {
      if (isAuthenticated) {
        try {
          const res = await axios.get('/api/chatbot/history');
          if (res.data.messages && res.data.messages.length > 0) {
            setMessages(res.data.messages);
          } else {
            // Add a welcome message if no history
            setMessages([
              {
                sender: 'bot',
                content: 'Welcome to the Citizen Grievance Redressal System! How can I help you today?'
              }
            ]);
          }
        } catch (error) {
          console.error('Error fetching chat history:', error);
        }
      } else {
        // Add a welcome message for non-authenticated users
        setMessages([
          {
            sender: 'bot',
            content: 'Welcome to the Citizen Grievance Redressal System! How can I help you today?'
          }
        ]);
      }
    };

    fetchSuggestedQuestions();
    fetchChatHistory();
  }, [isAuthenticated]);

  useEffect(() => {
    // Scroll to bottom whenever messages change
    scrollToBottom();
    
    // If voice is enabled and a new bot message is added, speak it
    const lastMessage = messages[messages.length - 1];
    if (voiceEnabled && lastMessage && lastMessage.sender === 'bot' && !isSpeaking) {
      speakText(lastMessage.content);
    }
  }, [messages, voiceEnabled, isSpeaking]);

  useEffect(() => {
    if (audioQueue.length > 0 && !isSpeaking) {
      playNextInQueue();
    }
  }, [audioQueue, isSpeaking]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  const handleInputChange = (e) => {
    setInput(e.target.value);
    setShowSuggestions(true); // Show suggestions when typing
  };

  const handleInputFocus = () => {
    // Only show suggestions if user is authenticated
    if (isAuthenticated) {
      setShowSuggestions(true); // Show suggestions when input is focused
    }
  };

  const navigateToLogin = () => {
    onClose();
    navigate('/login');
  };

  const navigateToRegister = () => {
    onClose();
    navigate('/register');
  };

  const handleSendMessage = async (e) => {
    e.preventDefault();

    if (!input.trim()) return;

    // Check if user is authenticated
    if (!isAuthenticated) {
      // Add authentication required message
      const authMessage = {
        sender: 'bot',
        content: 'Please log in to use the chatbot. This helps us provide personalized assistance and track your grievances.'
      };

      setMessages(prev => [...prev, {
        sender: 'user',
        content: input
      }, authMessage]);

      setInput('');
      return;
    }

    // Add user message to chat
    const userMessage = {
      sender: 'user',
      content: input
    };

    setMessages(prev => [...prev, userMessage]);
    setInput('');
    setLoading(true);
    setShowSuggestions(false); // Hide suggestions when showing responses

    try {
      // Get response from chatbot API
      const res = await axios.post('/api/chatbot/query', { query: input });
      console.log('Chatbot response:', res.data);

      // Add bot response to chat
      const botMessage = {
        sender: 'bot',
        content: res.data.response
      };

      setMessages(prev => [...prev, botMessage]);
    } catch (error) {
      console.error('Error getting chatbot response:', error);

      // Add error message
      const errorMessage = {
        sender: 'bot',
        content: 'Sorry, I encountered an error. Please try again later.'
      };

      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setLoading(false);
    }
  };

  const handleSuggestedQuestion = async (question) => {
    // Check if user is authenticated
    if (!isAuthenticated) {
      // Add authentication required message
      const authMessage = {
        sender: 'bot',
        content: 'Please log in to use the chatbot. This helps us provide personalized assistance and track your grievances.'
      };

      setMessages(prev => [...prev, {
        sender: 'user',
        content: question
      }, authMessage]);

      return;
    }

    // Add user message to chat
    const userMessage = {
      sender: 'user',
      content: question
    };

    setMessages(prev => [...prev, userMessage]);
    setLoading(true);
    setShowSuggestions(false); // Hide suggestions when showing responses

    try {
      // Get response from chatbot API
      const res = await axios.post('/api/chatbot/query', { query: question });
      console.log('Chatbot response for suggested question:', res.data);

      // Add bot response to chat
      const botMessage = {
        sender: 'bot',
        content: res.data.response
      };

      setMessages(prev => [...prev, botMessage]);
    } catch (error) {
      console.error('Error getting chatbot response:', error);

      // Add error message
      const errorMessage = {
        sender: 'bot',
        content: 'Sorry, I encountered an error. Please try again later.'
      };

      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setLoading(false);
    }
  };

  // Speech recognition setup
  const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
  const recognition = SpeechRecognition ? new SpeechRecognition() : null;

  if (recognition) {
    recognition.continuous = false;
    recognition.interimResults = false;

    recognition.onresult = (event) => {
      const transcript = event.results[0][0].transcript;
      setInput(transcript);
      setIsListening(false);
    };

    recognition.onerror = (event) => {
      console.error('Speech recognition error', event.error);
      setIsListening(false);
    };

    recognition.onend = () => {
      setIsListening(false);
    };
  }

  const playNextInQueue = () => {
    if (audioQueue.length === 0) return;
    
    setIsSpeaking(true);
    const audioSrc = audioQueue[0];
    
    if (audioRef.current) {
      audioRef.current.src = audioSrc;
      audioRef.current.play();
    } else {
      // If audio element doesn't exist, create one
      const audio = new Audio(audioSrc);
      audio.onended = handleAudioEnded;
      audioRef.current = audio;
      audio.play();
    }
  };

  const handleAudioEnded = () => {
    setAudioQueue(prevQueue => prevQueue.slice(1));
    setIsSpeaking(false);
  };

  const toggleVoiceOutput = () => {
    setVoiceEnabled(!voiceEnabled);
    
    // Stop any current speech if turning off
    if (voiceEnabled && isSpeaking) {
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current.currentTime = 0;
      }
      setIsSpeaking(false);
      setAudioQueue([]);
    }
  };

  const speakText = async (text) => {
    if (!voiceEnabled) return;
    
    try {
      const response = await axios.post('/api/chatbot/text-to-speech', {
        text,
        language: 'en'
      });
      
      if (response.data.audio) {
        const audioSrc = `data:audio/mp3;base64,${response.data.audio}`;
        setAudioQueue(prevQueue => [...prevQueue, audioSrc]);
      }
    } catch (error) {
      console.error('Error converting text to speech:', error);
    }
  };

  const playMessageAudio = (messageContent) => {
    if (isSpeaking) {
      // Stop current speech
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current.currentTime = 0;
      }
      setIsSpeaking(false);
      setAudioQueue([]);
    }
    
    // Speak the selected message
    speakText(messageContent);
  };

  if (!show) return null;

  return (
    <Fade in={show}>
      <div className="chatbot-container">
        <div className="chatbot-header">
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <SmartToyIcon sx={{ mr: 1 }} />
            <Typography variant="h6">
              Citizen Assistant
            </Typography>
          </Box>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Tooltip title={voiceEnabled ? "Turn off voice" : "Turn on voice"}>
              <IconButton 
                color={voiceEnabled ? "secondary" : "default"} 
                onClick={toggleVoiceOutput}
                size="small"
                sx={{ mr: 1 }}
              >
                {voiceEnabled ? <VolumeUpIcon /> : <VolumeOffIcon />}
              </IconButton>
            </Tooltip>
            <IconButton color="inherit" onClick={onClose} aria-label="Close chatbot">
              <CloseIcon />
            </IconButton>
          </Box>
        </div>

        <div className="chatbot-messages">
          {messages.map((message, index) => (
            <div key={index} className={`chatbot-message ${message.sender}`}>
              {message.sender === 'bot' && (
                <Avatar className="chatbot-avatar bot-avatar" sx={{ bgcolor: 'primary.light' }}>
                  <SmartToyIcon fontSize="small" />
                </Avatar>
              )}
              <div className="chatbot-message-content">
                {message.content}
                {message.sender === 'bot' && (
                  <IconButton 
                    size="small" 
                    onClick={() => playMessageAudio(message.content)}
                    sx={{ ml: 1, opacity: 0.7, '&:hover': { opacity: 1 } }}
                  >
                    <VolumeUpIcon fontSize="small" />
                  </IconButton>
                )}
              </div>
              {message.sender === 'user' && (
                <Avatar className="chatbot-avatar user-avatar" sx={{ bgcolor: 'secondary.main' }}>
                  {isAuthenticated && user ? user.name.charAt(0) : 'U'}
                </Avatar>
              )}
            </div>
          ))}
          {loading && (
            <div className="chatbot-message bot">
              <Avatar className="chatbot-avatar bot-avatar" sx={{ bgcolor: 'primary.light' }}>
                <SmartToyIcon fontSize="small" />
              </Avatar>
              <div className="chatbot-message-content typing-indicator">
                <span></span>
                <span></span>
                <span></span>
              </div>
            </div>
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* Authentication message for non-authenticated users */}
        {!isAuthenticated && (
          <Paper elevation={3} sx={{ m: 2, p: 2, bgcolor: '#f8f9fa' }}>
            <Typography variant="body2" sx={{ mb: 2 }}>
              Please log in or register to use the chatbot for personalized assistance with your grievances.
            </Typography>
            <Box sx={{ display: 'flex', justifyContent: 'space-around' }}>
              <Button 
                variant="contained" 
                color="primary" 
                startIcon={<LoginIcon />}
                onClick={navigateToLogin}
                size="small"
              >
                Log In
              </Button>
              <Button 
                variant="outlined" 
                color="primary" 
                startIcon={<PersonAddIcon />}
                onClick={navigateToRegister}
                size="small"
              >
                Register
              </Button>
            </Box>
          </Paper>
        )}

        {/* Suggested questions section - now separate from input field */}
        {isAuthenticated && showSuggestions && (
          <div className="chatbot-floating-suggestions">
            <Box sx={{ display: 'flex', alignItems: 'center', px: 2, py: 1 }}>
              <QuestionAnswerIcon fontSize="small" sx={{ mr: 1, color: 'primary.main' }} />
              <Typography variant="subtitle2" color="primary.main">
                Suggested Questions
              </Typography>
            </Box>
            <div className="chatbot-suggestions-scroll">
              <div className="chatbot-suggestions-horizontal">
                {suggestedQuestions.map((question, index) => (
                  <Tooltip title={question} key={index} placement="top" arrow>
                    <Zoom in={true} style={{ transitionDelay: `${index * 50}ms` }}>
                      <div 
                        className="chatbot-suggestion"
                        onClick={() => handleSuggestedQuestion(question)}
                      >
                        {question.length > 30 ? `${question.substring(0, 27)}...` : question}
                      </div>
                    </Zoom>
                  </Tooltip>
                ))}
              </div>
            </div>
          </div>
        )}

        <form onSubmit={handleSendMessage} className="chatbot-input">
          <input
            type="text"
            value={input}
            onChange={handleInputChange}
            onFocus={handleInputFocus}
            placeholder={isListening ? "Listening..." : "Type your message..."}
            disabled={loading || isListening}
            aria-label="Type your message"
          />
          {recognition && (
            <Tooltip title={isListening ? "Stop listening" : "Start voice input"}>
              <IconButton 
                onClick={() => {
                  if (!isListening) {
                    recognition.start();
                    setIsListening(true);
                  } else {
                    recognition.stop();
                    setIsListening(false);
                  }
                }} 
                disabled={loading}
                color={isListening ? "error" : "default"}
                sx={{ 
                  mx: 0.5,
                  animation: isListening ? 'pulse 1.5s infinite' : 'none',
                  '@keyframes pulse': {
                    '0%': { opacity: 1 },
                    '50%': { opacity: 0.5 },
                    '100%': { opacity: 1 }
                  }
                }}
              >
                {isListening ? <MicIcon fontSize="small" /> : <MicOffIcon fontSize="small" />}
              </IconButton>
            </Tooltip>
          )}
          <button type="submit" disabled={loading || isListening} aria-label="Send message">
            {loading ? <CircularProgress size={20} color="inherit" /> : <SendIcon fontSize="small" />}
          </button>
        </form>
        
        {/* Voice controls */}
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', p: 1, borderTop: '1px solid #eee' }}>
          <FormControlLabel
            control={
              <Switch 
                checked={voiceEnabled} 
                onChange={toggleVoiceOutput} 
                color="primary" 
              />
            }
            label={
              <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center' }}>
                <VolumeUpIcon fontSize="small" sx={{ mr: 0.5 }} />
                Voice Output
              </Typography>
            }
          />
        </Box>
        
        {/* Hidden audio element */}
        <audio ref={audioRef} style={{ display: 'none' }} />
      </div>
    </Fade>
  );
};

export default Chatbot;
